import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';
// createAPI   RTK-Querry,  endpoints,      API
// fetchBaseQuerry     fetch,  404  json.stringify(parse),   axios;

const baseEndpointURL = 'https://638bb7497220b45d22958e91.mockapi.io/api/main';

export const contactsApi = createApi({
  reducerPath: 'contactsApi',
  // reducerPath: 'contactsApi'-  ,     
  baseQuery: fetchBaseQuery({
    // baseQuery -   fetch   API-,     ;
    baseUrl: baseEndpointURL,
    //   Url   Url,
    //     baseQuery
  }),
  // tagTypes -   
  tagTypes: ['Contacts'],
  endpoints: builder => ({
    // endpoints  ,   ,    ,    (get, post, update, delete)
    fetchContactsList: builder.query({
      // builder   query
      query: () => '/contacts',
      //   Contacts        
      providesTags: ['Contacts'],
    }),

    deleteContact: builder.mutation({
      query: contactId => ({
        url: `/contacts/${contactId}`,
        method: 'DELETE',
      }),
      //  ()   ,    
      invalidatesTags: ['Contacts'],
    }),

    createContact: builder.mutation({
      query: contactContent => ({
        url: '/contacts',
        method: 'POST',
        body: contactContent,
        // 
        // body: {
        //   name: contactContent.name,
        //   phone: contactContent.phone
        // },
      }),
      //  ()     
      invalidatesTags: ['Contacts'],
    }),
  }),
});

//    http-, RTK-Query   
//     use     F  f  fetch
//     useCreateContactMutation  
//   HTTP- (get, post, put, delete)
export const {
  useFetchContactsListQuery,
  useDeleteContactMutation,
  useCreateContactMutation,
} = contactsApi;
